.cart__wrapper {
  position: fixed;
  top: 60px;
  right: 20px;
  border-radius: 12px;
  background-color: white;
  border: 1px solid gray;
  padding: 10px;
  z-index: 10;
}

.cart__innerWrapper {
  height: -webkit-max-content;
  height: -moz-max-content;
  height: max-content;
  max-height: 50vh;
  overflow-y: auto;
}

.Cart__cartTotal {
  margin-top: 10px;
  display: flex;
  justify-content: space-between;
}

.fa-times-circle {
  color: gray;
  transition: transform 0.2s linear;
}

.fa-times-circle:hover {
  transform: rotate(90deg);
}

.CartItem__item {
  border-bottom: 1px solid gray;
  padding: 10px 0;
  margin-top: 10px;
}

li {
  list-style-type: none;
  height: 100px;
  display: flex;
  grid-gap: 5px;
  gap: 5px;
  align-items: center;
}

li img {
  height: 100%;
  width: 100px;
  object-fit: cover;
}
.CartItem__item:nth-child(1) {
  border-top: 1px solid gray;
}

.CartItem__button {
  margin-left: auto;
  padding: 4px 8px;
  border-radius: 4px;
  border: none;
  cursor: pointer;
}

.CartItem__button:focus,
.CartItem__button:active {
  outline: none;
}

.products__wrapper {
  display: flex;
  flex-wrap: wrap;
  max-width: 1024px;
  width: 80%;
  margin: 70px auto 0;
  grid-gap: 12px;
  gap: 12px;
}

.productCard__wrapper {
  width: 30%;
  padding: 10px;
  border: 1px solid gray;
  border-radius: 12px;
  margin: auto;
  word-wrap: break-word;
}

.productCard__img {
  object-fit: cover;
  flex-shrink: 1;
  width: 100%;
  height: 281px;
  margin-bottom: 20px;
}

.ProductCard__Rateing {
  margin: 20px 0;
}
.ProductCard__price {
  margin: 20px 0;
}

.ProductCard__button {
  padding: 6px 10px;
  cursor: pointer;
  border-radius: 25px;
  background-color: #ffa430;
  border: none;
  transition: all 0.2s ease-in;
}

.ProductCard__button:hover {
  color: white;
  background-color: #f1b668;
  transform: translate(2px, -2px);
}

.ProductCard__button:focus,
.ProductCard__button:active {
  outline: none;
}

@media screen and (max-width: 1147px) {
  .productCard__wrapper {
    width: 45%;
  }
}

@media screen and (max-width: 700px) {
  .productCard__wrapper {
    width: 90%;
  }
}

